* {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
}

body {
  max-width: 800px;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  font-size: 62.5%;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  padding: 0 1rem;
}

input.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}

input.btn:hover {
  background: #666;
  transform: scale(1.04);
}